*{
  box-sizing: border-box;
  font-family: 'Inter', sans-serif;
  font-family: 'Karla', sans-serif;
}
body{
    height: 100vh;
    margin: 0;
    padding: 0;
    background-color: #F5F7FB;
    /* for bachground image to be displayed at bottom, height parameter must be declared */
    /* e.g. height:100vh; */
    background: url("./images/blob-bottom.png") bottom left no-repeat, 
        url("./images/blob-top.png") top right no-repeat;
    background-size: 10% , 15%;
}

