*{
    box-sizing: border-box;
    font-family: 'Inter', sans-serif;
    font-family: 'Karla', sans-serif;
}
body{
    font-family: 'Karla';
    font-style: normal;
    font-weight: 700;
    font-size: 31.25px;
    line-height: 37px;
    /* text-align: center; */

    color: #293264;

    height: 100vh;
    /* height: max-content; */
    margin: 0;
    padding: 20px;
    background-color: #F5F7FB;
    /* for bachground image to be displayed at bottom, height parameter must be declared */
    /* e.g. height:100vh; */
    background: url("../images/blob-bottom.png") bottom left no-repeat, 
        url("../images/blob-top.png") top right no-repeat;
    background-size: 10% , 15%;
}
.disclaimer-text{
    margin-top: 3em;
    font-family: 'Inter';
    font-style: normal;
    font-weight: 400;
    font-size: 0.5em;
    line-height: 29px;
    color:#b5c0f7 ;
    letter-spacing: 1px;
    text-align: center;
}
.Intro-title{
    font-family: 'Karla';
    font-style: normal;
    font-weight: 700;
    font-size: 31.25px;
    line-height: 37px;
    text-align: center;
}
.Intro-main{
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-top: 85px;
}
.Intro-desc{
    font-family: 'Inter';
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 19px;
    text-align: center;
}
.Intro-btn{
    width: 193px;
    height: 52px;
    background: #4D5B9E;
    border-radius: 15px;
    font-family: 'Inter';
    font-style: normal;
    font-weight: 500;
    font-size: 16px;
    line-height: 19px;
    text-align: center;

    color: #F5F7FB;
}
.Questions-question{
    font-family: 'Karla';
    font-style: normal;
    font-weight: 700;
    font-size: 16px;
    line-height: 19px;
    /* identical to box height */

}
.Questions-choices{
    font-family: 'Inter';
    font-style: normal;
    font-weight: 500;
    font-size: 10.24px;
    line-height: 12px;
    /* identical to box height */

    /* text-align: center; */
}
.Question-choice{
    padding: 3px 15px;
    margin: 7px;
    border: 0.794239px solid #4D5B9E;
    border-radius: 7.94239px;
}
.Questions-btn{
    background: #4D5B9E;
    border: none;
    border-radius: 10px;
    width: 120px;
    height: 35px;

    font-family: 'Inter';
    font-style: normal;
    font-weight: 600;
    font-size: 10.24px;
    line-height: 12px;
    text-align: center;

    color: #F5F7FB;
}
.scoreText{
    font-family: 'Inter';
    font-style: normal;
    font-weight: 700;
    font-size: 12.8px;
    line-height: 15px;
    text-align: center;

    color: #293264;
}
@media (orientation: portrait ) {
    body{
        background-size: 20% ,25%;
    }
    .Intro-main{
        padding-top: 300px;
    }
  }